:root {
  /* color */
  --color_main: #997dff;
}

* {
  margin: 0;
  color: inherit;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1;
  text-align: center;
}

body {
  color: #222;
  padding: 30px 30px 0;
}

h1 {
  font-size: 25px;
}

h2 {
  padding: 20px;
  font-size: 20px;
}

button {
  border: 0;
  background: none;
}

.quiz_wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 400px;
  height: 400px;
  margin: auto;
  padding: 50px;
  border-radius: 15px;
  box-sizing: border-box;
  box-shadow: 3px 8px 20px rgba(0, 0, 0, 0.3);
  background: #fff;
}

.quiz_wrap_bg {
  border: 0;
  background: var(--color_main);
}

.quiz_wrap_bg h1,
.quiz_wrap_bg h2 {
  color: #fff;
}

.question h1,
.answer h1,
.score h1 {
  margin-bottom: 15px;
}

.question h2,
.answer h2 {
  word-break: keep-all;
  line-height: 1.5;
}

.OXButton {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.OXButton button {
  width: calc(50% - 10px);
  height: 90px;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: var(--color_main);
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  cursor: pointer;
}

.answer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.answer button,
.score p {
  display: block;
  width: 100%;
  height: 90px;
  margin: 0 auto;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: #fff;
  color: var(--color_main);
  font-size: 20px;
  font-weight: 600;
  line-height: 90px;
  cursor: pointer;
}

.answer .svg-inline--fa.fa-angle-right {
  margin-left: 10px;
}

.answer .svg-inline--fa.fa-angle-right path {
  color: var(--color_main);
}

.score {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.score .score_box {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.score .score_box p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  line-height: 1.5;
  cursor: auto;
}

.score .score_box p b {
  display: block;
  font-size: 14px;
}